import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./asset-brand-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class AssetBrandAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;

 
assetBrandForm: FormGroup;
assetbrandId = 0;  
ajaxData : AssetBrandData[];
assetbrand= new AssetBrandData();
status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.assetbrandId).subscribe(params=>{
			if(params.assetbrandId != undefined){
				this.assetbrandId=params.assetbrandId; 
			}
		}); 
	console.log("assetbrandId=="+this.assetbrandId);
	if(this.assetbrandId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.assetBrandForm = new FormGroup({
			'assetbrandId' : new FormControl(''),
			'assetbrandName' : new FormControl('', [ Validators.required]),
			'assetbrandEntryId' : new FormControl(''),
			'assetbrandEntryDate' : new FormControl(''),
			'assetbrandModifiedId' : new FormControl(''),
			'assetbrandModifiedDate' : new FormControl(''),
		});
	}
	  
	   	get assetbrandName(){ return this.assetBrandForm.get('assetbrandName')};
	    get assetbrandEntryId(){ return this.assetBrandForm.get('assetbrandEntryId')};
	    get assetbrandEntryDate(){ return this.assetBrandForm.get('assetbrandEntryDate')};
	    get assetbrandModifiedId(){ return this.assetBrandForm.get('assetbrandModifiedId')};
	    get assetbrandModifiedDate(){ return this.assetBrandForm.get('assetbrandModifiedDate')};
	 
	  populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<AssetBrandData>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/assetbrand-data ',
							 			JSON.stringify({ 'assetbrandId' :this.assetbrandId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.assetbrand = data['populateData'][0];
																		console.log("assetbrandModifiedId=="+this.assetbrand.assetbrandModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['service/asset-brand-list']);      //path should be changed
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();

			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.assetbrand[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.assetBrandForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.assetbrandId == 0){ 
				this.assetbrand['requestType'] = 'add';
			}else{
				this.assetbrand['requestType'] = 'update';
				this.assetbrand['assetbrandId'] = this.assetbrandId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/assetbrand',
									JSON.stringify(this.assetbrand),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
				  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.assetBrandForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.assetbrand);
	  }
 
  onDelete(){
		 this.assetbrand['requestType'] = 'delete';
		this.assetbrand['assetbrandId'] = this.assetbrandId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/assetbrand',
									JSON.stringify(this.assetbrand),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.assetBrandForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

export class  AssetBrandData {
    assetbrandId: string;
    assetbrandName: string;
	assetbrandEntryId: string;
	assetbrandEntryBy: string;
	assetbrandEntryDate: string;
	assetbrandModifiedId: string;
	assetbrandModifiedBy: string;
	assetbrandModifiedDate: string;
  
}


